$primary-color: white;
$bg: black;
body {
  color: $bg;
  background-color: $primary-color;
}
.form {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.devices {
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.phones {
  display: flex;
}

iframe {
  padding: 25px;
}

.form-field {
  padding: 10px;
}

.device-container {
  display: flex;
  flex-direction: column;
}
